* {
  box-sizing: border-box;
  color: #000;
}

body {
  object-fit: fill;
  opacity: 0;
  background: url("../../canvas-bg.fc761c9d.jpg");
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100svh;
  transition: all .5s;
  display: flex;
}

.main-content {
  position: relative;
}

.clock_hands {
  transition: all 1s linear;
  position: absolute;
  top: 0;
  left: 0;
}

.clock_shadow {
  z-index: -1;
  filter: blur(8px) brightness(0);
  opacity: .25;
  transition: all 1s linear;
  position: absolute;
  top: 12px;
  left: 6px;
}

.clock_hands_shadow {
  filter: blur(4px) brightness(0);
  opacity: .15;
  transition: all 1s linear;
  position: absolute;
  top: 6px;
  left: 3px;
}

.clock_hands_shadow_more {
  filter: blur(2px) brightness(0);
  opacity: .3;
  transition: all 1s linear;
  position: absolute;
  top: 4px;
  left: 2px;
}

.clock_shadow_more {
  z-index: -1;
  filter: blur(2px) brightness(0);
  opacity: .15;
  transition: all 1s linear;
  position: absolute;
  top: 8px;
  left: 2px;
}

#clock_d {
  transform: scale(1.5);
}

.footer {
  z-index: 1;
  flex-direction: column;
  align-items: center;
  gap: .25em;
  width: 100%;
  padding: .5em;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

.footer * {
  color: #00000080;
  gap: 1em;
  line-height: 1.5em;
  text-decoration: none;
  list-style: none;
  transition: all .15s;
  display: flex;
}

.footer :focus, .footer :hover {
  color: #000;
}

ul {
  margin: 0;
  padding: 0;
}

